    <h1 class="page-title">Launch Your Meme Token</h1>
    
    <div class="launch-container">
      <div class="info-section">
        <div class="info-card">
          <h2>Create Your Own Meme Token on Solana</h2>
          <p>Launch your meme token on pump.fun directly through our platform. Simply fill out the form, pay the fee, and get your token live on Solana!</p>
          
          <div class="features">
            <div class="feature">
              <i class="fas fa-rocket"></i>
              <span>Fast Launch</span>
            </div>
            <div class="feature">
              <i class="fas fa-shield-alt"></i>
              <span>Secure</span>
            </div>
            <div class="feature">
              <i class="fas fa-coins"></i>
              <span>Squad</span>
            </div>
          </div>
          
          <div class="requirements">
            <h3>Requirements:</h3>
            <ul>
              <li>Connect your Solana wallet</li>
              <li>Prepare a logo for your token (PNG or JPG, max 500KB)</li>
              <li>Have at least 0.1 SOL for fees</li>
            </ul>
          </div>
        </div>
      </div>
      
      <div class="form-section">
        <div class="form-card">
          <h2>Token Details</h2>
          
          <div v-if="!walletConnected" class="wallet-warning">
            <i class="fas fa-exclamation-triangle"></i>
            <p>Please connect your wallet to create a token</p>
            <button class="connect-wallet-btn" @click="connectWallet">Connect Wallet</button>
          </div>
          
          <form v-else @submit.prevent="handleSubmit" class="token-form">
            <div class="form-group">
              <label for="tokenName">Token Name*</label>
              <input 
                id="tokenName" 
                v-model="formData.name" 
                type="text" 
                required 
                placeholder="e.g., Awesome Meme Token"
                :class="{ 'error': errors.name }"
              >
              <span v-if="errors.name" class="error-message">{{ errors.name }}</span>
            </div>
            
            <div class="form-group">
              <label for="tokenSymbol">Token Symbol*</label>
              <input 
                id="tokenSymbol" 
                v-model="formData.symbol" 
                type="text" 
                required 
                placeholder="e.g., AMT"
                :class="{ 'error': errors.symbol }"
              >
              <span v-if="errors.symbol" class="error-message">{{ errors.symbol }}</span>
            </div>
            
            <div class="form-group">
              <label for="tokenSupply">Total Supply*</label>
              <input 
                id="tokenSupply"
                type="number" 
                disabled 
                placeholder="1 000 000 000"
              >
            </div>
            
            <div class="form-group">
              <label for="tokenLogo">Token Logo*</label>
              <div class="logo-upload">
                <input 
                  id="tokenLogo" 
                  type="file" 
                  @change="handleLogoUpload" 
                  accept="image/png, image/jpeg"
                  :class="{ 'error': errors.logo }"
                >
                <div v-if="logoPreview" class="logo-preview">
                  <img :src="logoPreview" alt="Logo Preview">
                </div>
              </div>
              <span v-if="errors.logo" class="error-message">{{ errors.logo }}</span>
            </div>
            
            <div class="fee-info">
              <h3>Fee Information</h3>
              <p>Creation Fee: 0.05 SOL</p>
              <p>Platform Fee: 0.01 SOL</p>
              <p>Total: 0.06 SOL</p>
            </div>
            
            <div class="form-actions">
              <button 
                type="submit" 
                class="submit-btn" 
                :disabled="isSubmitting || !walletConnected"
              >
                <span v-if="isSubmitting">
                  <i class="fas fa-spinner fa-spin"></i> Processing...
                </span>
                <span v-else>Launch Token</span>
              </button>
            </div>
          </form>
        </div>
      </div>
    </div>
    
    <!-- Success Modal -->
    <div v-if="showSuccessModal" class="modal-overlay">
      <div class="success-modal">
        <div class="modal-header">
          <h2>Token Created Successfully!</h2>
          <button class="close-btn" @click="showSuccessModal = false">×</button>
        </div>
        <div class="modal-body">
          <div class="success-icon">
            <i class="fas fa-check-circle"></i>
          </div>
          <div class="token-details">
            <p><strong>Token Name:</strong> {{ createdToken.name }}</p>
            <p><strong>Token Symbol:</strong> {{ createdToken.symbol }}</p>
            <p><strong>Token Address:</strong> 
              <a :href="'https://solscan.io/token/' + createdToken.address" target="_blank">
                {{ createdToken.address }}
              </a>
            </p>
          </div>
          <div class="actions">
            <button class="action-btn" @click="copyTokenAddress">
              <i class="fas fa-copy"></i> Copy Address
            </button>
            <a :href="'https://solscan.io/token/' + createdToken.address" target="_blank" class="action-btn">
              <i class="fas fa-external-link-alt"></i> View on Solscan
            </a>
          </div>
        </div>
      </div>
    </div>
