<script>
import { ref, reactive, computed, onMounted } from 'vue';
import { useStore } from 'vuex';
import { useWallet } from 'solana-wallets-vue';
import { Connection, PublicKey, Transaction } from '@solana/web3.js';
import bs58 from 'bs58';
import axios from 'axios';
// import Toast from '@/components/Toast.vue';

export default {
  name: 'MemeTokenLaunchPage',
  components: {
    // Toast
  },
  setup() {
    const store = useStore();
    const { connected, publicKey, signTransaction, connect, select } = useWallet();
    
    // State
    const formData = reactive({
      name: '',
      symbol: '',
      logo: null
    });
    
    const errors = reactive({
      name: '',
      symbol: '',
      logo: ''
    });
    
    const logoPreview = ref(null);
    const isSubmitting = ref(false);
    const showSuccessModal = ref(false);
    const createdToken = reactive({
      name: '',
      symbol: '',
      address: ''
    });
    
    // Computed
    const walletConnected = computed(() => connected.value);
    
    // Methods
    const connectWallet = async () => {
      try {
        if (!connected.value) {
          // Выбираем Phantom кошелек и подключаемся
          select('Phantom');
          await connect();
        }
      } catch (error) {
        console.error('Error connecting wallet:', error);
        store.dispatch('showToast', {
          message: 'Failed to connect wallet. Please try again.',
          type: 'error'
        });
      }
    };
    
    const handleLogoUpload = (event) => {
      const file = event.target.files[0];
      if (!file) return;
      
      // Проверка типа файла
      if (!['image/jpeg', 'image/png'].includes(file.type)) {
        errors.logo = 'Logo must be a PNG or JPG file';
        return;
      }
      
      // Проверка размера файла (500KB = 512000 bytes)
      if (file.size > 512000) {
        errors.logo = 'Logo must be less than 500KB';
        return;
      }
      
      // Сбрасываем ошибку
      errors.logo = '';
      formData.logo = file;
      
      // Создаем превью
      const reader = new FileReader();
      reader.onload = (e) => {
        logoPreview.value = e.target.result;
      };
      reader.readAsDataURL(file);
    };
    
    const validateForm = () => {
      let isValid = true;
      
      // Валидация имени
      if (!formData.name.trim()) {
        errors.name = 'Token name is required';
        isValid = false;
      } else if (formData.name.length > 50) {
        errors.name = 'Token name must be less than 50 characters';
        isValid = false;
      } else {
        errors.name = '';
      }
      
      // Валидация символа
      if (!formData.symbol.trim()) {
        errors.symbol = 'Token symbol is required';
        isValid = false;
      } else if (formData.symbol.length > 10) {
        errors.symbol = 'Token symbol must be less than 10 characters';
        isValid = false;
      } else {
        errors.symbol = '';
      }
      
      // Валидация лого
      if (!formData.logo) {
        errors.logo = 'Token logo is required';
        isValid = false;
      }
      
      return isValid;
    };
    
    const uploadLogo = async () => {
      try {
        const formData = new FormData();
        formData.append('logo', formData.logo);
        
        const response = await axios.post('/upload', formData, {
          headers: {
            'Content-Type': 'multipart/form-data'
          }
        });
        
        return response.data.logoUrl;
      } catch (error) {
        console.error('Error uploading logo:', error);
        throw new Error('Failed to upload logo');
      }
    };
    
    const handleSubmit = async () => {
      if (!validateForm()) return;
      
      try {
        isSubmitting.value = true;
        
        // 1. Загружаем лого и получаем URL
        let logoUrl = '';
        if (formData.logo) {
          const formDataWithLogo = new FormData();
          formDataWithLogo.append('logo', formData.logo);
          
          const uploadResponse = await axios.post('/upload', formDataWithLogo, {
            headers: {
              'Content-Type': 'multipart/form-data'
            }
          });
          
          logoUrl = uploadResponse.data.logoUrl;
        }
        
        // 2. Получаем инструкции для создания токена
        const instructionsResponse = await axios.post('/meme-token/create-instructions', {
          name: formData.name,
          symbol: formData.symbol,
          walletAddress: publicKey.value.toString(),
          logoUrl: logoUrl
        });
        
        // 3. Десериализуем транзакцию
        const serializedTransaction = instructionsResponse.data.serializedTransaction;
        const transactionBuffer = Buffer.from(serializedTransaction, 'base64');
        const transaction = Transaction.from(transactionBuffer);
        
        // 4. Получаем последний blockhash
        const connection = new Connection('https://sleek-icy-frog.solana-mainnet.quiknode.pro/9d4845e2d4c60caf45dc4f10d0bdcfdbcd6a321c/');
        const { blockhash, lastValidBlockHeight } = await connection.getLatestBlockhash();
        
        // 5. Обновляем blockhash и feePayer
        transaction.recentBlockhash = blockhash;
        transaction.feePayer = publicKey.value;
        
        // 6. Подписываем транзакцию
        const signedTransaction = await signTransaction(transaction);
        
        // 7. Отправляем подписанную транзакцию на сервер
        const createResponse = await axios.post('/meme-token/create', {
          signedTransaction: bs58.encode(signedTransaction.serialize())
        });
        
        // 8. Обрабатываем успешный ответ
        const { tokenAddress } = createResponse.data;
        
        // 9. Получаем информацию о созданном токене
        const tokenInfoResponse = await axios.get(`/meme-token/${tokenAddress}`);
        const tokenInfo = tokenInfoResponse.data;
        
        // 10. Показываем модальное окно с успешным созданием
        createdToken.name = tokenInfo.name || formData.name;
        createdToken.symbol = tokenInfo.symbol || formData.symbol;
        createdToken.address = tokenAddress;
        showSuccessModal.value = true;
        
        // 11. Сбрасываем форму
        formData.name = '';
        formData.symbol = '';
        formData.logo = null;
        logoPreview.value = null;
        
        // Показываем уведомление об успехе
        store.dispatch('showToast', {
          message: 'Token created successfully!',
          type: 'success'
        });
      } catch (error) {
        console.error('Error creating token:', error);
        store.dispatch('showToast', {
          message: error.response?.data?.message || 'Failed to create token. Please try again.',
          type: 'error'
        });
      } finally {
        isSubmitting.value = false;
      }
    };
    
    const copyTokenAddress = () => {
      navigator.clipboard.writeText(createdToken.address);
      store.dispatch('showToast', {
        message: 'Token address copied to clipboard!',
        type: 'success'
      });
    };
    
    onMounted(() => {
      // Проверяем, подключен ли кошелек
      if (!connected.value) {
        store.dispatch('showToast', {
          message: 'Please connect your wallet to create a token',
          type: 'info'
        });
      }
    });
    
    return {
      formData,
      errors,
      logoPreview,
      isSubmitting,
      walletConnected,
      showSuccessModal,
      createdToken,
      connectWallet,
      handleLogoUpload,
      handleSubmit,
      copyTokenAddress
    };
  }
};
</script>

